
@import url('https://fonts.googleapis.com/css2?family=League+Script&display=swap');

body {
  font-size: 1rem;
  text-align: center;
  margin: 0;
  text-align: center;
  /* background-color:rgba(175,238,238, 0.5)
   */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navUl{
  background-color:rgb(156, 68, 130, 0.3);
  padding: 0;
  margin: 1rem 0 3rem 0;
  display: flex;
  justify-content: center;
}
li{
  margin:  0 4rem;
}
.navLink{
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  /* box-shadow: 0px 0px 10px 35px rgb(156 68 130 / 10%); */
  
}

.header{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
.profile_box{
  border: 2px solid thistle;
  border-radius: 999px;
}
.profile_box:hover {
  box-shadow: 0px 0px 18px 15px thistle;
  cursor: pointer;
}

.profile{
  width: 6rem;
  padding: 0.3rem;
}
.profile a{
  font-family: fantasy;
  font-size: 1.3rem;
  text-decoration: none;
  color: black;
  font-stretch: 150%;
 
  

}
.date{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.datepicker{
  width: 5.6rem;
  height: 3.5rem;
  font-family: fantasy;
  font-size: 1.2rem;
  cursor: pointer;
  border: 2px solid thistle;
  border-radius: 999px;
  padding: 0.5rem ;
}
.datepicker:hover{
  box-shadow: 0px 0px 18px 15px thistle;
}
h1{
  font-size: 4rem;
  /* font-family: 'League Script', cursive; */
  color: white;
  background-color: rgb(0,206,209);
  padding: 1rem 0;
  margin: 3rem 32%;
  border-radius: 30px;
  font-weight: 900;
  animation: fade-in 3s ;
  -webkit-text-fill-color: rgb(0,206,209); 
  -webkit-text-stroke-width: 2px;
  /* -webkit-text-stroke: 2px; */
  text-decoration: underline 5px white;
  
  
  
}
.h1_signin{
  font-size: 2.5rem;
  margin: 0 40%;
}

h2{
  font-family: 'League Script', cursive;
  font-size: 5rem;
  -webkit-text-fill-color: white; 
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #ff9999;
  
}
.h2_signin{
   font-size: 3rem;
}


.signin, .register{
  background-color:lemonchiffon;
  color: grey;
  margin-top: 2rem;
  width: 10%;
  height: 3rem;
  border-radius: 999px;
  border:1px solid yellow;
  animation: fade-in 7s ;
}
.signin:hover, .register:hover{
  background-color: rgb(223, 226, 26);
}
.a_signin{
  text-decoration: none;
  color: grey;
 
 
}
h3{
  background-color: rgba(140, 121, 154, 0.4);
  color:rgba(73, 50, 66, 0.8);
  margin: 0 25% ;
  padding: 2rem 0;
  font-weight: lighter;
}
.signin_form{
  background-color: rgba(140, 121, 154, 0.4);
  margin: 0 25% 2rem 25% ;
  height: 20rem;
 
}
.register_form{
  background-color: rgba(140, 121, 154, 0.4);
}
label{
  font-weight: bolder;
}
.signin_box input{
  border-radius: 30px;
  height: 2rem;
  width: 35%;
  margin: 0 0 1rem 0;
}
.signin_submit{
  width: 13%;
  font-size: 1.2rem;
  background-color: rgba(73, 50, 66, 0.8);
  color: white;
  cursor: pointer;
}
.form{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  
}
.inputitem{
  height: 1.8rem;
  width: 15rem;
  margin: 0.5rem 0.5rem;
  font-weight: bolder;
  border-radius: 5px;
}
.inputquantity{
  height: 1.5rem;
  width: 2rem;
  margin: 0.5rem 0.5rem;
  border-radius: 5px;
  
}
.submit{
  height: 2.5rem;
  width: 6rem;
  font-size: 1.1rem;
  border-radius: 30px;
  color: white;
  background-color: rgba(140, 121, 154, 1);
  margin-bottom: 1rem;
  cursor: pointer;
  
  
}
.container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

ul{
  width: 100%;
  padding: 2rem 5rem 2rem 25rem;
  background-color: rgb(156, 68, 130, 0.2);
  
}
.allBoxes{
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}
.box{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid white;
  border-radius: 15px;
  margin: 0.5rem 0;
  width: 60%;
  height: 5rem;
  /* background-color: rgba(199, 134, 179, 0.8); */
  cursor: pointer;
}


li{
  padding: 2rem 2rem;
  list-style: none;
  align-items: center;

}

p{
  font-size: 1.5rem;
  justify-content: center;
  color: rgb(243, 236, 236);
  text-transform: capitalize;
}
.price{
  border: 1px solid white;
  border-radius: 15px;
  margin: 0.5rem;
  height: 5rem;
  cursor: pointer;
  background: #77e0ee;
  opacity: 0.5;
  color: white;
}
.price p{
  margin: 0;
}
.quantity{
  color: rgb(112, 57, 151)
}
button{
 
  height: 2rem;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 5px;
  justify-content: flex-end;
  color: rgb(243, 217, 217);
  background-color: rgba(73, 50, 66, 0.8);
  margin: 0 1rem 0 0;
}

.Total{
  display: grid;
  grid-template-columns: 34rem 26rem;
  margin-top: 2rem;
  width: 55rem;
  border-radius: 999px;
  background: white;
  box-shadow: 0px 0px 10px 10px rgba(199, 134, 179, 0.2)
  
}
.boxTotal{
  display: flex;
  justify-content: space-between;
}
h4{
  font-size: 1.5rem;
  color: grey;
}
.wordTotal{
  padding-left: 6rem;
}

@keyframes fade-in {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@media only screen and (min-device-width:  280px) and (max-device-width:  767px){
  body{font-size: 0.8rem;margin: 1rem;}
  .navUl{margin: 1rem 0;}
  .navUl li{margin: 0; padding: 2rem 0;}
  .navLink{font-size: 0.8rem;padding: 0 1rem;}
  .header{justify-content: space-between;}
  .profile_box{border:none}
   h1{font-size: 2.5rem; padding: 1rem 0.5rem; margin: 1rem 10%;animation: none;}
   h2{font-size: 4.55rem;}
   .container{width: 100%;}
   .container ul{ display: flex; justify-content: center;}
   .allBoxes{justify-content: center;}
   .box{ width: 15rem;}
   .box li{padding: 2rem 0;margin: 0 2rem;}
  .Total{grid-template-columns: 13.5rem 9.5rem; width: unset;}
  .wordTotal{padding-left: 1rem;}
  .total{font-size: 1.2rem;}
  ul{padding: 1rem;}
  .price{width: 25%;}
 .price li{margin:0; padding: 2rem 1rem;}
 .totalPrice{padding-left: 1rem;}
  .profile{padding: 0;}
  .profile a{font-size: 1rem;}
  .datepicker{ padding: 0;font-size: 1rem; width: 5rem; border:none}
  .signin,.register{width: unset;}
  .h1_signin{font-size: 2.5rem; padding: 1rem 0.5rem; margin: 1rem 10%;}
  h3{margin: 0;}
  .signin_form{margin: 0 0 2rem 0;}

}

@media only screen and (min-device-width:  768px) and (max-device-width:  1365px){
  body{font-size: 1.2rem;}
}

@media only screen and (min-device-width: 1900px) {
  body{font-size: 1.5rem;}
} 